.stepwizard-step {
  display:table-cell;
  text-align:center;
  position:relative;
  p {
    margin-top:10px;
  }
  button[disabled] {
    opacity:1 !important;
    filter:alpha(opacity=100) !important;
  }
}
.stepwizard-row {
  display:table-row;
}
.stepwizard {
  display:table;
  width:100%;
  position:relative;
}
.stepwizard-row:before {
  top:14px;
  bottom:0;
  position:absolute;
  content:" ";
  width:100%;
  height:1px;
  background-color:#cccccc;
  z-order:0;
}
.btn-circle {
  width:30px;
  height:30px;
  text-align:center;
  padding:6px 0;
  font-size:12px;
  line-height:1.428571429;
  border-radius:15px;
}