.complex-uslug{
  a:hover{
    color: #fff;
    span{
      transition: 0.5s;
      background: #fff;
      i{
        transition: 0.8s;
        color: #a387c5;
      }
    }
  }

  a:not(:hover) {
    color: #fff;
    span{
      transition: 0.5s;
      i{
